  background-color: #e6e9ec;
  position: absolute;
  -webkit-transform: scaleX(0.75) rotate(45deg);
  -ms-transform: scaleX(0.75) rotate(45deg);
  transform: scaleX(0.75) rotate(45deg);
  border-width: 0;
